import { createStore, combineReducers } from "redux"
import { TopNavReducer } from '../components/TopNav/TopNav.reducer'
import { ProductListReducer } from '../components/ProductsList/ProductsList.reducer'
import { CartReducer } from "../components/Cart/Cart.reducer"
import { OrderFormReducer } from "../components/OrderForm/OrderForm.reducer"


const rootReducer = combineReducers({
    TopNavReducer,
    ProductListReducer,
    CartReducer,
    OrderFormReducer,
})

const store = createStore(rootReducer);

export default store;