import Constants from './ProductsList.constants'
import { IProductList, IProduct } from './ProductsList.interface'


export const setInitState = (payload:IProductList) => {
    return {type: Constants.SET_INIT_STATE, payload}
}

export const plusProduct = (product:IProduct) => {
    return {type: Constants.PLUS_PRODUCT, payload:product}
}

export const minusProduct = (product:IProduct) => {
    return {type: Constants.MINUS_PRODUCT, payload:product}
}

export const resetProduct = (product:IProduct) => {
    return {type: Constants.RESET_PRODUCT, payload:product}
}

export const changeFilter = (categoryId:number) => {
    return {type: Constants.CHANGE_FILTER, payload:categoryId}
}