import React from 'react'
import { connect } from 'react-redux'
import TopNavTemplate from './TopNav.template'
import { ITopNavInterface } from './TopNav.interface'


const mapStateToProps = (reducer:any):ITopNavInterface => {
    const { CartReducer } = reducer
    return {
        cartCount:CartReducer.totalCount,
    }
}

const TopNavComponent = (props:ITopNavInterface) => {
    return (<TopNavTemplate {...props}/>)
}

export default connect(mapStateToProps)(TopNavComponent)