import { IOrderForm } from "./OrderForm.interface"
import { OrderFormState, errorsInitState } from "./OrderForm.initState"
import Constants from './OrderForm.constants'


export const OrderFormReducer = (state:IOrderForm = OrderFormState, action:any) => {

    if (action.type === Constants.CHANGE_NAME) {
        return {
            ...state,
            user: {
                ...state.user,
                name: action.payload,
            }
        }
    }

    if (action.type === Constants.CHANGE_CELL) {
        let onlyDigits = action.payload.match(/\d/g)
        let cell:string = onlyDigits ? onlyDigits.join("") : ""
        if (cell.length > 9)
            cell = cell.slice(0, 9)
        return {
            ...state,
            user: {
                ...state.user,
                cell,
            }
        }
    }

    if (action.type === Constants.CHANGE_REGION) {
        return {
            ...state,
            deliveryData: {
                ...state.deliveryData,
                region: action.payload,
            }
        }
    }

    if (action.type === Constants.CHANGE_ADDRESS) {
        let address = action.payload
        if (address.length > 32)
            address = address.slice(0, 32)
        return {
            ...state,
            deliveryData: {
                ...state.deliveryData,
                address,
            }
        }
    }

    if (action.type === Constants.CHANGE_COMMENT) {
        let comment = action.payload
        if (comment.length > 256)
            comment = comment.slice(0, 256)
        return {
            ...state,
            deliveryData: {
                ...state.deliveryData,
                comment,
            }
        }
    }

    if (action.type === Constants.CHANGE_PAYMENT_METHOD) {
        let paymentType = action.payload
        return {
            ...state,
            paymentData: {
                ...state.paymentData,
                paymentType,
            }
        }
    }

    if (action.type === Constants.RESET_FORM) {
        return {
            ...OrderFormState
        }
    }

    if (action.type === Constants.RESET_ERRORS) {
        return {
            ...state,
            errors: {
                ...errorsInitState
            }
        }
    }

    if (action.type === Constants.SET_ERROR) {
        let newErrors = {...state.errors}
        const errorType = action.payload

        if (errorType === "name")
            newErrors.nameError = true
        if (errorType === "cell")
            newErrors.cellError = true
        if (errorType === "address")
            newErrors.addressError = true

        return {
            ...state,
            errors: {
                ...newErrors
            }
        }
    }

    return state
}