import React from 'react'
import { connect } from 'react-redux'
import { ProductListTemplate } from './ProductsList.template'
import { IProductList, IProduct, IProductListExt, ICategory } from './ProductsList.interface'
import { getAll, getProducts } from './ProductsList.helpers'
import { setInitState } from './ProductsList.actions'
import { ProductListInitState } from "./ProductsList.initState"
import { plusProduct, minusProduct, resetProduct, changeFilter } from './ProductsList.actions'
import { addProduct } from '../Cart/Cart.actions'
import { ICartProduct } from '../Cart/Cart.interface'


const mapStateToProps = (reducer:any):IProductList => {
    const { ProductListReducer } = reducer
    return ProductListReducer
}

const mapDispatchToProps = (dispatch:any) => {
    return {
        setInitState:(payload:IProductList) => dispatch(setInitState(payload)),
        plusProduct:(payload:IProduct) => dispatch(plusProduct(payload)),
        minusProduct:(payload:IProduct) => dispatch(minusProduct(payload)),
        resetProduct:(payload:IProduct) => dispatch(resetProduct(payload)),
        addProduct:(payload:ICartProduct) => {dispatch(addProduct(payload))},
        changeFilter:(payload:number) => {dispatch(changeFilter(payload))},
    }
}


class ProductListComponent extends React.Component<IProductListExt, IProductList> {

    constructor(props: IProductListExt, state: IProductList) {
        super(props, state)

        this.addProductToCart = this.addProductToCart.bind(this)
    }

    componentDidMount() {
        const { setInitState, categoryFilter } = this.props
        setInitState({
            ...ProductListInitState,
            loading: true,
        })

        const response = getProducts()

        response.then((response) => {
            let newProducts: IProduct[] = []
            let tmpProducts: IProduct[]
            let newCategories: ICategory[] = []
    
            for (let category of response.data) {
                tmpProducts = []
                for (let product of category.products) {
                    tmpProducts.push({
                        ...product,
                        count: 1
                    })
                }
    
                newCategories.push({
                    id: category.id,
                    name: category.name,
                    products: tmpProducts,
                })
                newProducts = [...newProducts, ...tmpProducts]
            }

            setInitState({
                categories: newCategories,
                products: newProducts,
                loading: false,
                loaded: true,
                categoryFilter,
            })
        })

    }

    addProductToCart(product:IProduct) {
        const { addProduct, resetProduct } = this.props
        addProduct({
            id:product.id,
            name:product.name,
            price:product.price,
            count:product.count,
            totalPrice:product.price * product.count
        })
        resetProduct(product)
    }

    render() {
        const { products, categories, loaded, loading, plusProduct,
            minusProduct, categoryFilter, changeFilter } = this.props
        return (
            <ProductListTemplate
                products={products}
                categories={categories}
                loaded={loaded}
                loading={loading}
                plusProduct={plusProduct}
                minusProduct={minusProduct}
                categoryFilter={categoryFilter}
                addProduct={product => this.addProductToCart(product)}
                changeFilter={changeFilter}
            />
        )
    }
    
}

export default connect(mapStateToProps, mapDispatchToProps)(ProductListComponent)