import React from 'react'
import { ICategory, IProductListTemplate, IProduct } from './ProductsList.interface'
import './ProductsList.css'


interface ICategoryTemplate {
    categories:ICategory[],
    changeFilter:(payload:number) => void,
}

interface ISingleProduct {
    product: IProduct,
    plusProduct:(payload:IProduct) => void,
    minusProduct:(payload:IProduct) => void,
    addProduct:(payload:IProduct) => void,

}

const CategoriesListTemplate = (props:ICategoryTemplate) => {
    const { categories, changeFilter } = props
    return (
        <ul className="list-group mb-5 categories-list">
        <li
            id="category-all"
            className="list-group-item category"
            onClick={() => changeFilter(0)}
        >Меню</li>
        {categories.map( (category, index) => {
            return (
                <li
                    key={`category-${index}`}
                    className="list-group-item category"
                    onClick={() => changeFilter(category.id)}
                >{category.name}</li>
            )
        } )}
        </ul>
    )
}

const SingleProduct = (props:ISingleProduct) => {
    const { product, addProduct, minusProduct, plusProduct } = props
    return (
        <div className="col-md-4 product-item">
            <div className="card mb-4 shadow-sm">
            {/* <svg className="bd-placeholder-img card-img-top" width="100%" height="225" xmlns="http://www.w3.org/2000/svg" preserveAspectRatio="xMidYMid slice" focusable="false" role="img" aria-label="Placeholder: Thumbnail"><title>Placeholder</title><rect width="100%" height="100%" fill="#55595c"></rect><text x="50%" y="50%" fill="#eceeef" dy=".3em">Thumbnail</text></svg> */}
            <img src={product.image} />
            <div className="card-body">
                <h3 className="product-item-title">{product.name}</h3>
                <p className="card-text product-item-desc">{product.description}</p>
                <h5 className="product-item-props">
                    <small className="text-muted mr-2">{product.weight} {product.weight_points}</small>
                    <span className="product-item-price">{product.price} грн</span>
                </h5>
                <hr/>
                <div className="d-flex justify-content-between align-items-center">
                    <button
                        type="button"
                        className="btn btn-sm btn-outline-custom"
                        onClick={() => addProduct(product)}
                    >Додати</button>
                    <div className="product-item-counter">
                        <span className="with-border minus" onClick={() => {minusProduct(product)}}>-</span>
                        <span className="with-border counter">{product.count}</span>
                        <span className="with-border plus" onClick={() => {plusProduct(product)}}>+</span>
                    </div>
                </div>
            </div>
            </div>
        </div>
    )
}


export const ProductListTemplate = (props:IProductListTemplate) => {
    const { categories, loading, loaded,
        plusProduct, minusProduct, addProduct, changeFilter, categoryFilter } = props

    if (!loading && loaded)
        return (
            <div className="ProductList">

                <CategoriesListTemplate categories={categories} changeFilter={changeFilter}/>

                    {categories.map( (category) => {
                        if (category.id === categoryFilter || !categoryFilter)
                        return (
                            <div key={`category-${category.id}`} className="mb-3">
                                <h1 className="category-name">{category.name}</h1>
                                <div className="row">
                                {category.products.map( product => {
                                    return (
                                        <SingleProduct
                                            key={`product-${product.id}`}
                                            product={product}
                                            plusProduct={plusProduct}
                                            minusProduct={minusProduct}
                                            addProduct={addProduct}
                                        />
                                    )
                                })}
                                </div>
                            </div>
                        )
                    })}                  

                <CategoriesListTemplate categories={categories} changeFilter={changeFilter}/>

            </div>
        )
    else if (loading && loaded)
        return (
            <h1>ERROR</h1>
        )
    else if (loading && !loaded)
        return (
            <div className="spinner-border" role="status">
                <span className="sr-only">Loading...</span>
            </div>
        )
    else
        return (
            <h1></h1>
        )
}