import axios from 'axios'
import { MAIN_BACKEND } from '../../constants'


interface CompactProduct {
    id:number,
    count:number,
}

export const sendOrderData = (data:any) => {
    try {
        const { user, deliveryData, paymentData, products } = data
        let compactProducts:CompactProduct[] = []

        if (products.length === 0)
            return false

        for (let product of products) {
            compactProducts.push({
                id:product.id,
                count:product.count,
            })
        }
        const postData = {
            user_name: user.name,
            phone_number: user.cell,
            address: deliveryData.address,
            region: deliveryData.region,
            comment: deliveryData.comment,
            compact_products: compactProducts
        }

        axios.post(`${MAIN_BACKEND}/order/`, postData).then((res) => {
            window.location.href = "/success"
        }).catch((err) => {
            window.location.href = "/failed"
        })

        return true
    } catch (error) {
        return false
    }
} 