import Constants from './OrderForm.constants'


export const changeName = (payload:string) => {
    return {type:Constants.CHANGE_NAME, payload}
}

export const changeCell = (payload:string) => {
    return {type:Constants.CHANGE_CELL, payload}
}

export const changeAddress = (payload:string) => {
    return {type:Constants.CHANGE_ADDRESS, payload}
}

export const changeRegion = (payload:string) => {
    return {type:Constants.CHANGE_REGION, payload}
}

export const changeComment = (payload:string) => {
    return {type:Constants.CHANGE_COMMENT, payload}
}

export const changePaymentMethod = (payload:string) => {
    return {type:Constants.CHANGE_PAYMENT_METHOD, payload}
}

export const resetForm = () => {
    return {type:Constants.RESET_FORM, payload:{}}
}

export const resetErrors = () => {
    return {type:Constants.RESET_ERRORS, payload:{}}
}

export const setError = (name:string) => {
    return {type:Constants.SET_ERROR, payload:name}
}