import { ICartState, ICartProduct } from "./Cart.interface"
import { CartInitState } from "./Cart.initState"
import Constants from './Cart.constants'


export const CartReducer = (state:ICartState = CartInitState, action:any):ICartState => {

    if (action.type === Constants.ADD_PRODUCT) {
        const newProduct:ICartProduct = action.payload
        let newProducts:ICartProduct[] = []
        let newTotalCount = 0
        let newTotalPrice = 0
        let exist = false
        for (let product of state.products) {
            if (product.id === newProduct.id) {
                newProducts.push(newProduct)
                newTotalCount += newProduct.count
                newTotalPrice += newProduct.totalPrice
                exist = true
            } else {
                newProducts.push(product)
                newTotalCount += product.count
                newTotalPrice += product.totalPrice
            }
        }
        if (!exist) {
            newProducts.push(newProduct)
            newTotalCount += newProduct.count
            newTotalPrice += newProduct.totalPrice
        }
        const result = {
            products: newProducts,
            totalCount: newTotalCount,
            totalPrice: newTotalPrice,
        }
        return result
    }

    if (action.type === Constants.REMOVE_PRODUCT) {
        let newProducts:ICartProduct[] = []
        const targetProduct:ICartProduct = action.payload
        for (let product of state.products) {
            if (product.id !== targetProduct.id) {
                newProducts.push(product)
            }
        }
        return {
            products:newProducts,
            totalCount: state.totalCount - targetProduct.count,
            totalPrice: state.totalPrice - targetProduct.totalPrice,
        }
    }

    if (action.type === Constants.PLUS_PRODUCT) {
        let newProducts:ICartProduct[] = []
        const targetProduct:ICartProduct = action.payload
        for (let product of state.products) {
            if (product.id === targetProduct.id) {
                newProducts.push({
                    ...product,
                    count: product.count + 1,
                    totalPrice: product.totalPrice + product.price,
                })
            } else {
                newProducts.push(product)
            }
        }
        return {
            products: newProducts,
            totalCount: state.totalCount + 1,
            totalPrice: state.totalPrice + targetProduct.price
        }
    }

    if (action.type === Constants.MINUS_PRODUCT) {
        let newProducts:ICartProduct[] = []
        const targetProduct:ICartProduct = action.payload
        let newTotalCount = 0
        let newTotalPrice = 0
        for (let product of state.products) {
            if (product.id === targetProduct.id) {
                const newCount = product.count - 1
                if (newCount >= 1) {
                    newProducts.push({
                        ...product,
                        count: newCount,
                        totalPrice: product.price * newCount
                    })    
                    newTotalCount += newCount
                    newTotalPrice += product.price * newCount
                }
                    
            } else {
                newProducts.push(product)
                newTotalCount += product.count
                newTotalPrice += product.totalPrice
            }
        }
        return {
            products: newProducts,
            totalCount: newTotalCount,
            totalPrice: newTotalPrice,
        }
    }

    if (action.type === Constants.CLEAN_CART) {
        return {
            products:[],
            totalCount: 0,
            totalPrice: 0,
        }
    }

    return state
}