import Constants from './Cart.constants'
import { ICartProduct } from './Cart.interface'


export const addProduct = (product:ICartProduct) => {
    return {type:Constants.ADD_PRODUCT, payload:product}
}

export const removeProduct = (product:ICartProduct) => {
    return {type:Constants.REMOVE_PRODUCT, payload:product}
}

export const plusProduct = (product:ICartProduct) => {
    return {type:Constants.PLUS_PRODUCT, payload:product}
}

export const minusProduct = (product:ICartProduct) => {
    return {type:Constants.MINUS_PRODUCT, payload:product}
}

export const cleanCart = () => {
    return {type:Constants.CLEAN_CART, payload:{}}
}