import { IProductList, IProduct, ICategory } from "./ProductsList.interface"
import { ProductListInitState } from "./ProductsList.initState"
import Constants from './ProductsList.constants'


export const ProductListReducer = (state:IProductList = ProductListInitState, action:any) => {

    if (action.type === Constants.SET_INIT_STATE) {
        return action.payload
    }

    if (action.type === Constants.PLUS_PRODUCT) {
        const targetProduct = action.payload
        let newProducts: IProduct[]
        let newCategories: ICategory[] = []
        for (let category of state.categories) {

            if (category.id === targetProduct.category) {
                newProducts = []
                for (let product of category.products) {
                    if (product.id === targetProduct.id) {
                        newProducts.push({
                            ...product,
                            count: product.count + 1
                        })
                    } else {
                        newProducts.push(product)
                    }
                }
                newCategories.push({
                    ...category,
                    products:newProducts
                })
            } else {
                newCategories.push(category)
            }  
        }
        
        return {
            ...state,
            categories:newCategories,
        }
    }

    if (action.type === Constants.MINUS_PRODUCT) {
        const targetProduct = action.payload
        let newProducts: IProduct[]
        let newCategories: ICategory[] = []

        for (let category of state.categories) {

            if (category.id === targetProduct.category) {
                newProducts = []
                for (let product of category.products) {
                    if (product.id === targetProduct.id) {
                        const newCount = product.count > 1 ? product.count - 1 : 1
                        newProducts.push({
                            ...product,
                            count: newCount
                        })
                    } else {
                        newProducts.push(product)
                    }
                }
                newCategories.push({
                    ...category,
                    products:newProducts
                })
            } else {
                newCategories.push(category)
            }  
        }
        
        return {
            ...state,
            categories:newCategories,
        }
    }

    if (action.type === Constants.RESET_PRODUCT) {
        const targetProduct = action.payload
        let newProducts: IProduct[]
        let newCategories: ICategory[] = []
        for (let category of state.categories) {

            if (category.id === targetProduct.category) {
                newProducts = []
                for (let product of category.products) {
                    if (product.id === targetProduct.id) {
                        newProducts.push({
                            ...product,
                            count: 1
                        })
                    } else {
                        newProducts.push(product)
                    }
                }
                newCategories.push({
                    ...category,
                    products:newProducts
                })
            } else {
                newCategories.push(category)
            }  
        }
        
        return {
            ...state,
            categories:newCategories,
        }
    }

    if (action.type === Constants.CHANGE_FILTER) {
        return {
            ...state,
            categoryFilter:action.payload,
        }
    }

    return state
}