import React from 'react'
import { ICartStateExt } from './Cart.interface'
import './Cart.css'


export const CartTemplate = (props:ICartStateExt) => {
    const { products, totalCount, totalPrice, plusProduct, minusProduct, removeProduct } = props
    return (
        <div className="CartTemplate">
            <h4 className="d-flex justify-content-between align-items-center mb-3">
              <span>Ваше замовлення</span>
              <span className="badge badge-custom badge-pill">{totalCount}</span>
            </h4>
            <ul className="list-group mb-3">
              {products.map( (product, index ) => {
                return (
                  <li key={`cart-item-${index}`} className="list-group-item cart-item">
                    <div className="d-flex justify-content-between lh-condensed mb-2">
                      <h6 className="my-0">{product.name}</h6>
                      <span>{product.price}грн X {product.count} = {product.totalPrice}грн</span>
                    </div>
                    <div className="d-flex justify-content-between">
                      <div className="cart-item-counter">
                          <span className="with-border minus" onClick={() => {minusProduct(product)}}>-</span>
                          <span className="with-border counter">{product.count}</span>
                          <span className="with-border plus" onClick={() => {plusProduct(product)}}>+</span>
                      </div>
                      <div>
                        <span className="with-border remove" onClick={() => removeProduct(product)}>X</span>
                      </div>
                    </div>
                  </li>  
                )
              })}
              <li className="list-group-item d-flex justify-content-between">
                <span>Всього</span>
                <strong>{totalPrice}грн</strong>
              </li>
            </ul>
        </div>
    )
}