import React from 'react'
import { IOrderFormTemplate } from './OrderForm.interface'
import './OrderForm.css'


export const OrderFormTemplate = (props:IOrderFormTemplate) => {
    const {
        user,
        deliveryData,
        paymentData,
        changeName,
        changeCell,
        changeAddress,
        changeRegion,
        changeComment,
        changePaymentMethod,
        createOrder,
        errors,
    } = props
    return (
        <form className="needs-validation OrderFormTemplate" onSubmit={createOrder}>
            <div className="mb-3">
                <label htmlFor="address">Ім'я</label>
                <input
                    type="text"
                    className="form-control"
                    id="name"
                    placeholder=""
                    onChange={changeName}
                    value={user.name}
                />
                <div className={`invalid-feedback ${errors.nameError ? "d-block" : ""}`}>
                Вкажіть ваше ім'я.
                </div>
            </div>

            <div className="mb-3">
                <label htmlFor="cell">Номер телефону</label>
                <div className="input-group">
                <div className="input-group-prepend">
                    <span className="input-group-text">+380</span>
                </div>
                <input
                    type="text"
                    className="form-control"
                    id="cell"
                    placeholder="XXXXXXXXX"
                    onChange={changeCell}
                    value={user.cell}
                    />
                <div className={`invalid-feedback ${errors.cellError ? "d-block" : ""}`} style={{width: "100%"}}>
                    Номер телефону є обов'язковим.
                </div>
                </div>
            </div>

            <div className="mb-3">
                <label htmlFor="city">Регіон</label>
                <select
                    onChange={changeRegion}
                    value={deliveryData.region}
                    className="custom-select d-block w-100"
                    id="city"
                >
                    <option value="lviv">Львів</option>
                    <option value="lviv region">Околиці Львова</option>
                    <option value="by-client">Самовивіз (знижка 10%)</option>
                </select>
            </div>

            <div className="mb-3">
                <label htmlFor="address">Адреса</label>
                <input
                    type="text"
                    className="form-control"
                    id="address"
                    placeholder="вул. Шевченка 1, кв 1"
                    onChange={changeAddress}
                    value={deliveryData.address}
                />
                <div className={`invalid-feedback ${errors.addressError ? "d-block" : ""}`}>
                Введіть адресу доставки.
                </div>
            </div>

            <div className="mb-3">
                <label htmlFor="comment">Коментарій</label>
                <textarea
                    className="form-control"
                    id="comment"
                    rows={5}
                    placeholder="додаткові пропозиції..."
                    onChange={changeComment}
                    value={deliveryData.comment}
                ></textarea>
            </div>

            <hr className="mb-4" />

            <h4 className="mb-3">Тип оплати</h4>
            <div className="d-block my-3">
                <div className="custom-control custom-radio">
                    <input
                        id="cash"
                        name="paymentMethod"
                        type="radio"
                        className="custom-control-input"
                        checked={paymentData.paymentType == '1'}
                        value={'1'}
                        onChange={changePaymentMethod}
                    />
                    <label className="custom-control-label" htmlFor="cash">Готівка</label>
                </div>
                <div className="custom-control custom-radio">
                    <input
                        id="debit"
                        name="paymentMethod"
                        type="radio"
                        className="custom-control-input"
                        checked={paymentData.paymentType == '2'}
                        value={'2'}
                        onChange={changePaymentMethod}
                    />
                    <label className="custom-control-label" htmlFor="debit">Карткою при отриманні</label>
                </div>
                {/* <div className="custom-control custom-radio">
                    <input
                        id="debit"
                        name="paymentMethod"
                        type="radio"
                        className="custom-control-input"
                        disabled
                    />
                    <label className="custom-control-label" htmlFor="debit">Карткою онлайн (не досту)</label>
                </div> */}
            </div>

            <hr className="mb-4" />
            <button className="btn btn-custom btn-lg btn-block" type="submit">Створити замовлення</button>
        </form>
    )
}