import React from 'react'
import { connect } from 'react-redux'
import { IOrderForm, IOrderFormExt } from './OrderForm.interface'
import { OrderFormTemplate } from './OrderForm.template'
import {
    changeName,
    changeCell,
    changeAddress,
    changeRegion,
    changeComment,
    changePaymentMethod,
    resetForm,
    resetErrors,
    setError,
} from './OrderForm.actions'
import { sendOrderData } from './OrderForm.helpers'


const mapStateToProps = (reducer:any):IOrderForm => {
    const { OrderFormReducer, CartReducer } = reducer
    return {
        ...OrderFormReducer,
        products:CartReducer.products,
    }
}

const mapDispatchToProps = (dispatch:any) => {
    return {
        changeName:(payload:string) => dispatch(changeName(payload)),
        changeCell:(payload:string) => dispatch(changeCell(payload)),
        changeAddress:(payload:string) => dispatch(changeAddress(payload)),
        changeRegion:(payload:string) => dispatch(changeRegion(payload)),
        changeComment:(payload:string) => dispatch(changeComment(payload)),
        changePaymentMethod:(payload:string) => dispatch(changePaymentMethod(payload)),
        resetForm:() => dispatch(resetForm()),
        resetErrors:() => dispatch(resetErrors()),
        setError:(name:string) => dispatch(setError(name)),
    }
}


class OrderFormComponent extends React.Component<IOrderFormExt, IOrderForm> {

    constructor(props:IOrderFormExt, state:IOrderForm) {
        super(props, state)

        this.changeFormName = this.changeFormName.bind(this)
        this.changeFormCell = this.changeFormCell.bind(this)
        this.changeFormAddress = this.changeFormAddress.bind(this)
        this.changeFormRegion = this.changeFormRegion.bind(this)
        this.changeFormComment = this.changeFormComment.bind(this)
        this.changeFormPaymentMethod = this.changeFormPaymentMethod.bind(this)
        this.createOrder = this.createOrder.bind(this)
    }

    changeFormName(e:any) {
        const { changeName } = this.props
        changeName(e.target.value)
    }

    changeFormCell(e:any) {
        const { changeCell } = this.props
        changeCell(e.target.value)
    }

    changeFormAddress(e:any) {
        const { changeAddress } = this.props
        changeAddress(e.target.value)
    }

    changeFormRegion(e:any) {
        const { changeRegion } = this.props
        changeRegion(e.target.value)
    }

    changeFormComment(e:any) {
        const { changeComment } = this.props
        changeComment(e.target.value)
    }

    changeFormPaymentMethod(e:any) {
        const { changePaymentMethod } = this.props
        changePaymentMethod(e.target.value)
    }

    createOrder(e:any) {
        e.preventDefault()
        const { setError, resetErrors } = this.props
        const { user, deliveryData, paymentData, products } = this.props
        resetErrors()
        let isValid = true

        if (user.name.length === 0){
            setError("name")
            isValid = false
        }
        if (user.cell.length < 9){
            setError("cell")
            isValid = false
        }
        if (deliveryData.address.length < 5){
            setError("address")
            isValid = false
        }
        if (isValid) {
            sendOrderData({user, deliveryData, paymentData, products})
        }
    }

    render() {
        const {
            user,
            deliveryData,
            paymentData,
            errors,
        } = this.props
        return (
            <OrderFormTemplate
                user={user}
                deliveryData={deliveryData}
                paymentData={paymentData}
                changeName={this.changeFormName}
                changeCell={this.changeFormCell}
                changeAddress={this.changeFormAddress}
                changeRegion={this.changeFormRegion}
                changeComment={this.changeFormComment}
                changePaymentMethod={this.changeFormPaymentMethod}
                createOrder={this.createOrder}
                errors={errors}
            />
        )
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(OrderFormComponent)