import React from 'react'
import { connect } from 'react-redux'
import { ICartState, ICartStateExt, ICartProduct } from './Cart.interface'
import { CartTemplate } from './Cart.template'
import { plusProduct, minusProduct, removeProduct } from '../Cart/Cart.actions'


const mapStateToProps = (reducer:any):ICartState => {
    const { CartReducer } = reducer
    return CartReducer
}

const mapDispatchToProps = (dispatch:any) => {
    return {
        plusProduct:(product:ICartProduct) => dispatch(plusProduct(product)),
        minusProduct:(product:ICartProduct) => dispatch(minusProduct(product)),
        removeProduct:(product:ICartProduct) => dispatch(removeProduct(product)),
    }
}

class CartComponent extends React.Component<ICartStateExt, ICartState> {
    render() {
        return (
            <CartTemplate {...this.props}/>
        )
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(CartComponent)